@import url('https://fonts.googleapis.com/css?family=Roboto');

.App{
  font-family: 'Roboto', sans-serif;
}
.container{
  margin-left: 10%;
  margin-top: 5%;
  width: 720px;
  height: 550px;
  display: table;
  -webkit-perspective: 1000px;
          perspective: 1000px;
}
.card-side{
  overflow: hidden;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}
.card-body {
  width: 100%;
  height: 100%;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  -webkit-transition: all .7s linear;
  -o-transition: all .7s linear;
  transition: all .7s linear;
  -webkit-box-shadow: 0 10px 35px rgba(50,50,93,.1),0 2px 15px rgba(0,0,0,.07);
          box-shadow: 0 10px 35px rgba(50,50,93,.1),0 2px 15px rgba(0,0,0,.07);
}
.container:hover .card-body{
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}
.container:hover> .card-body > .f{
  opacity: 0;
  visibility: hidden;
  -webkit-transition: opacity 1s ease-in, visibility .75s linear;
  -o-transition: opacity 1s ease-in, visibility .75s linear;
  transition: opacity 1s ease-in, visibility .75s linear;
}



