.heading h2{
	font-size: 30px;
	margin-left: 35%;
}
.form{
	-ms-flex-line-pack: center;
	    align-content: center;
	font-weight: bold;
	font-size: 25px;
	padding:0 5%;
}
.row{
	padding: 10px;
}
input,
textarea {
  padding: 8px;
  width: 100%;
  border-top: 0;
  border-right: 0;
  border-bottom: 1px solid #eee;
  border-left: 0;
  
  &:focus {
    outline: 0;
    border-bottom: 1px solid #0c81f6;
  }
}

textarea {
  max-height: 80px;
  resize: vertical;
}

.btn input{
	max-width: 35%;
	padding: 5%;	
	background: #5cb85c;
	border-radius: 5px;
	font-weight: bold;
	color: #ffffff;
	-webkit-box-shadow: 13px 5px 17px #eee;
	        box-shadow: 13px 5px 17px #eee;
}
.btn input:hover{
	background: #5cd85c;
}
.btn{
	padding: 30px;
	margin-left: 35%;
}
.b{
  z-index: 2;
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}



